.header-desktop,
.header-desktop * {
  box-sizing: border-box;
}
.header-desktop {
  background: #ffffff;
  padding: 16px 0px 16px 0px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 0px;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  display: inline-flex;
  padding: 16px 0px;
  flex-direction: column;
  align-items: flex-start;
  @media screen and (max-width: 1279px) {
    padding: 0px 20px 12px 20px;
    display: flex;
    width: 412px;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
    position: relative;
  }
}
.header-row-1 {
  background: var(--neutrals-8, #fcfcfd);
  border-style: solid;
  border-color: var(--primary-softdenim, #b9c5ca);
  border-width: 0px 0px 0.7px 0px;
  padding: 8px 70px 8px 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
  width: 1440px;
  position: relative;
  overflow: hidden;
  @media screen and (max-width: 1279px) {
    background: #ffffff;
    border-style: solid;
    border-color: var(--primary-pasteltahoesnow, #d8e2e6);
    border-width: 0px 0px 1px 0px;
    display: inline-flex;
    padding: 0;
    flex-direction: initial;
    align-items: flex-start;
    justify-content: space-between;
    flex-shrink: 0;
    position: relative;
    width: 100%;
  }
}
.logo-header-desktop {
  flex-shrink: 0;
  width: 152px;
  height: 66px;
  position: relative;
  overflow: visible;
  @media screen and (max-width: 1279px) {
    flex-shrink: 0;
    width: 105px;
    height: 45px;
    position: relative;
    overflow: visible;
  }
}
.account-cart-and-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
  display: flex;
  flex-direction: row;
  gap: 8px;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
  @media screen and (max-width: 1279px) {
    flex-direction: row;
    align-items: center;
    gap: 16px;
    justify-content: flex-end;
  }
}

.mobileInput {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  position: relative;
}

.account {
  display: flex;
  flex-direction: row;
  gap: 11px;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.icons {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  margin-right: 12px;
  position: relative;
  overflow: visible;
  @media screen and (max-width: 1279px) {
    margin-right: 0;
  }
}
.account2 {
  color: var(--Text-River-Styx, #0c1012);
  font-family: HvDTrial Brandon Text;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  position: relative;
  height: 56px;
  width: 125px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.account2:hover,
.cart2:hover,
.label:hover {
  background-color: rgb(218, 234, 227);
  border-radius: 10px;
}

.cart {
  display: flex;
  flex-direction: row;
  gap: 11px;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
  @media screen and (max-width: 1279px) {
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    position: relative;
  }
}
.iconsMob {
  margin: 0;
  @media screen and (min-width: 1280px) {
    display: none;
  }
}
.mobileInput {
  margin: 0;
  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.cart2 {
  color: var(--Text-River-Styx, #0c1012);
  font-family: HvDTrial Brandon Text;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  position: relative;
  height: 56px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  width: 190px;
  height: 50px;
  cursor: pointer;
}

.label {
  color: var(--accent-midsummer, #336666);
  text-align: center;
  font-family: var(
    --buttons-font-family,
    'HvDTrialBrandonText-Bold',
    sans-serif
  );
  font-size: var(--buttons-font-size, 16px);
  line-height: var(--buttons-line-height, 18px);
  font-weight: var(--buttons-font-weight, 700);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Тут панель категорий неправильно отображается, не могу поменять что бы они были слева и внутри них отступ должен быть до инпута в 412px. Это основной див нижнего хедера */
.header-row-2 {
  border-style: solid;
  border-color: var(--primary-softdenim, #b9c5ca);
  border-width: 0px 0px 1px 0px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  padding-left: 70px;
  flex-shrink: 0;
  width: 1440px;
  position: relative;
  @media screen and (max-width: 1279px) {
    /* при мобильном тоже неправильно показывается менюшка, должно быть 4 чипсы сверху и две снизу, неправильне размеры */
    display: flex;
    padding: 0;
    border-width: 0px 0px 0px 0px;
    flex-direction: row;
    gap: 8px;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    flex-shrink: 0;
    width: 372px;
    position: relative;
  }
}
/* вот менюшка */
.menu-items {
  display: flex;
  padding: 8px 0px 0px 0px;
  align-items: center;
  align-self: stretch;
  @media screen and (max-width: 1279px) {
    padding-top: 0;
    border-radius: 8px;
    border-style: solid;
    border-color: var(--primary-pasteltahoesnow, #d8e2e6);
    border-width: 1px;
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    position: relative;
    overflow: hidden;
  }
}
.state-layer {
  padding: 8px 15.5px 8px 15.5px;
  display: flex;
  flex-direction: row;
  gap: 0px;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
}

.all-goods,
.living,
.dining,
.bedroom,
.sale,
.accessories {
  color: var(--text-river-styx, #0c1012);
  text-align: left;
  font-family: var(
    --titlesmall-font-family,
    'HvDTrialBrandonText-Medium',
    sans-serif
  );
  font-size: var(--titlesmall-font-size, 16px);
  line-height: var(--titlesmall-line-height, 22px);
  font-weight: var(--titlesmall-font-weight, 500);
  position: relative;
  cursor: pointer;
  text-decoration: none;
  transition: transform 0.3s;
  @media screen and (max-width: 1279px) {
    color: var(--text-river-styx, #0c1012);
    text-align: center;
    font-family: var(
      --titlesmall-font-family,
      'HvDTrialBrandonText-Medium',
      sans-serif
    );
    font-size: var(--titlesmall-font-size, 16px);
    line-height: var(--titlesmall-line-height, 22px);
    font-weight: var(--titlesmall-font-weight, 500);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.all-goods::after,
.living::after,
.dining::after,
.bedroom::after,
.sale::after,
.accessories::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: transparent;
  transition: background-color 0.3s, transform 0.3s;
  transform: translateY(-1px);
}

.all-goods:hover::after,
.living:hover::after,
.dining:hover::after,
.bedroom:hover::after,
.sale:hover::after,
.accessories:hover::after {
  background-color: rgb(57, 57, 66);
  transform: translateY(-1px);
}

.all-goods:hover,
.living:hover,
.dining:hover,
.bedroom:hover,
.sale:hover,
.accessories:hover {
  transform: translateY(-1px);
}

.search {
  display: flex;
  width: 520px;
  height: 56px;
  padding: 8px 180px 8px 12px;
  align-items: center;
  gap: 7px;
  flex-shrink: 0;
}

.icons3 {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  position: relative;
  overflow: visible;
}
::placeholder {
  color: var(--greyforplaceholders, #727678);
  text-align: left;
  font-family: 'ProximaNova-Regular', sans-serif;
  font-size: 14px;
  font-weight: 400;
  position: relative;
}

@media screen and (max-width: 1279px) {
  .button,
  .account2,
  .cart2,
  .search,
  .account2:hover,
  .cart2:hover,
  .label:hover {
    display: none;
  }
}
