@import-normalize; /* bring in normalize.css styles */

@font-face {
  font-display: swap;
  font-family: 'HvDTrialBrandonText-Medium';
  font-style: normal;
  font-weight: 500;
  src: url('fonts/HvDTrial_Brandon_Text_Medium-BF6514e9eb272a3.otf');
  src: url('fonts/HvDTrial_Brandon_Text_Medium-BF6514e9eb272a3.otf')
    format('otf');
}

@font-face {
  font-display: swap;
  font-family: 'HvDTrialBrandonText-Regular';
  font-style: normal;
  font-weight: 400;
  src: url('fonts/HvDTrial_Brandon_Text_Regular-BF6514e9eb617a0.otf');
  src: url('fonts/HvDTrial_Brandon_Text_Regular-BF6514e9eb617a0.otf')
    format('otf');
}

@font-face {
  font-display: swap;
  font-family: 'HvDTrialBrandonText-Bold';
  font-style: normal;
  font-weight: 700;
  src: url('fonts/HvDTrial_Brandon_Text_Bold-BF6514e9eb720dc.otf');
  src: url('fonts/HvDTrial_Brandon_Text_Bold-BF6514e9eb720dc.otf') format('otf');
}

@font-face {
  font-display: swap;
  font-family: 'Adelle-SemiBold';
  font-style: normal;
  font-weight: 600;
  src: url('fonts/Adelle_Semibold.otf');
  src: url('fonts/Adelle_Semibold.otf') format('otf');
}

body {
  margin: 0;
  font-family: 'HvDTrialBrandonText-Regular', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

li {
  list-style: none;
}

button {
  cursor: pointer;
}

a,
button,
input,
select,
h1,
h2,
h3,
h4,
h5,
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
  text-decoration: none;
  appearance: none;
  background: none;

  -webkit-font-smoothing: antialiased;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
